.section{
	&-5,
	&-10,
	&-12{
		margin-top: 250px;
	}
	&-2,
	&-3,
	&-4,
	&-7,
	&-8,
	&-9,
	&-11{
		padding-top: 250px;
	}
}


.hero-block{
	background: rgba(0, 0, 0, 0.25);
	position: relative;
	height: 100vh;
	min-height: 800px;
	max-height: 1500px;

	&__bg{
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		position: absolute;
		left: 0;
		top: 0;
		z-index: -1;
	}

	&__container{
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	&__content{
		text-align: center;
		color: #ffffff;
	}
	&__title{
		font-family: $secondFont;
		font-style: normal;
		font-weight: normal;
		font-size: 90px;
		line-height: 130%;
		letter-spacing: 0.04em;
		text-transform: uppercase;
		color: $pink;
	}
	&__subtitle{
		font-weight: 600;
		font-size: 40px;
		line-height: 130%;
		font-family: $titleFont;
	}
	&__text{
		margin-top: 55px;
		font-style: normal;
		font-weight: normal;
		font-size: 25px;
		line-height: 52px;
		text-align: center;
		span{
			font-weight: 600;
			font-size: 40px;
		}
	}
	&__btn{
		margin-top: 81px;
	}
}


.problem{
	&__container{}
	&__section{}
}
.sp1{
	display: flex;
	align-items: center;
	justify-content: space-between;
	&__left{
		width: 50%;
		&-block{
			position: relative;
		}
		&-img{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translateX(-50%) translateY(-50%);
		}
		&-text{
			font-family: $secondFont;
			font-style: normal;
			font-weight: normal;
			font-size: 45px;
			line-height: 130%;
			text-align: center;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			span{
				color: $pink;
			}
		}
	}
	&__right{
		width: 50%;
		&-img{}
	}
}
.sp2{
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	&__left{
		width: 50%;
		&-img{}
	}
	&__right{
		width: 50%;
		&-block{}
		&-img{}
		&-text{}
	}
}
.sp3{
	&__block{}
	&__heart{}
	&__text{}
	&__img{}
}



.dual{
	display: flex;
	min-height: 708px;
	box-sizing: border-box;
	width: 100%;
	&__left{
		width: 50%;
	}
	&__right{
		width: 50%;
		box-sizing: border-box;
		padding: 60px 60px 60px 156px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		background: $orange;
	}
	&__img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	&__text{
		max-width: 640px;
		font-family: $titleFont;
		font-style: normal;
		font-weight: normal;
		font-size: 40px;
		line-height: 130%;
		color: #FFFFFF;
	}

	&--invert{
		flex-direction: row-reverse;
		.dual__right{
			padding: 60px 130px 60px 60px;
			justify-content: flex-end;
		}
	}
}


.list{
	&__container{}
	&__title{
		font-family: $secondFont;
		font-style: normal;
		font-weight: normal;
		font-size: 55px;
		line-height: 130%;
		text-align: center;
		letter-spacing: 0.06em;
		text-transform: uppercase;
	}
	&__flex{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 161px;
	}
	&__item{
		margin-bottom: 53px;
		width: calc(50% - 30px);
		position: relative;
		padding-left: 77px;
		font-style: normal;
		font-weight: normal;
		font-size: 22px;
		line-height: 29px;
		min-height: 87px;

		&::before{
			content: "";
			display: block;
			width: 38px;
			height: 28px;
			background: url("../images/check.svg") center center no-repeat;
			background-size: contain;
			background-position: left top;
			position: absolute;
			left: 0;
			top: 7px;
		}
	}

	&__banner{
		width: 100%;
		min-height: 209px;
		padding: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #F7F7F7;

		&-text{
			font-family: $secondFont;
			font-style: normal;
			font-weight: normal;
			font-size: 46px;
			line-height: 130%;
			text-align: center;
			letter-spacing: 0.06em;
			text-transform: uppercase;
			color: $orange;
			span{
				color: $pink;
			}
		}
	}
}

.fullimage{
	position: relative;
	width: 100%;
	min-height: 888px;
	box-sizing: border-box;
	background: rgba(0, 0, 0, 0.45);
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 40px 0;

	&__bg{
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		position: absolute;
		left: 0;
		top: 0;
		z-index: -1;
	}
	&__container{}
	&__text{
		max-width: 940px;
		margin: 0 auto;
		text-align: center;
		font-family: $secondFont;
		font-style: normal;
		font-weight: normal;
		font-size: 45px;
		line-height: 130%;
		letter-spacing: 0.04em;
		text-transform: uppercase;
		color: #FFFFFF;
		span{
			color: $orange;
		}
	}
}


.quotes{
	background: $pink;
	min-height: 1030px;
	padding: 40px;
	color: #ffffff;
	display: flex;
	align-items: center;
	justify-content: center;
}

.author{
	max-width: 1920px;
	margin: 0 auto;
	min-height: 665px;
	&__flex{
		display: flex;
	}
	&__left{
		width: 50%;
	}
	&__right{
		width: 50%;
		img{
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__name{
		font-family: $secondFont;
		font-style: normal;
		font-weight: normal;
		font-size: 32px;
		line-height: 72px;
		letter-spacing: 0.04em;
		text-transform: uppercase;
		span{
			color: $orange;
			font-size: 	55px;
		}
	}
	&__info{
		margin-top: 27px;
		max-width: 470px;
		text-align: center;
		font-style: normal;
		font-weight: 500;
		font-size: 19px;
		line-height: 130%;
		text-transform: uppercase;
	}
	&__quote{
		margin-top: 68px;
		padding: 65px 64px 59px 140px;
		border: 1px solid $pink;
		box-sizing: border-box;
		border-radius: 10px;
		position: relative;

		&::before{
			content: "";
			display: block;
			width: 67px;
			height: 67px;
			background: url("../images/q.png") center center no-repeat;
			background-size: contain;
			position: absolute;
			top: 31px;
			left: 26px;
		}
		&__text{
			font-family: $titleFont;
			font-style: normal;
			font-weight: normal;
			font-size: 19px;
			line-height: 130%;
		}
	}
}


.registration{
	width: 100%;
	position: relative;
	padding: 250px 0 305px;
	background: rgba(0, 0, 0, 0.45);
	color: #ffffff;
	&__bg{
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
		position: absolute;
		left: 0;
		top: 0;
		z-index: -1;
	}
	&__container{}

	&__title{
		font-family: $secondFont;
		font-style: normal;
		font-weight: normal;
		font-size: 90px;
		line-height: 130%;
		text-align: center;
		letter-spacing: 0.04em;
		text-transform: uppercase;
		color: $orange;
	}
	&__subtitle{
		font-family: $titleFont;
		font-style: normal;
		font-weight: normal;
		font-size: 40px;
		line-height: 130%;
		text-align: center;
	}
	&__form{
		margin-top: 128px;
	}
}

.rform{
	&__label{
		font-family: $secondFont;
		font-style: normal;
		font-weight: normal;
		font-size: 70px;
		line-height: 130%;
		letter-spacing: 0.06em;
		text-transform: uppercase;
		margin-bottom: 75px;
	}
	&__block{
		width: 460px;
		margin: 0 auto;
		+.rform__block{
			margin-top: 43px;
		}

		&--flex{
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
	&__title{
		font-family: $secondFont;
		font-style: normal;
		font-weight: normal;
		font-size: 30px;
		line-height: 130%;
		letter-spacing: 0.04em;
		color: #FFFFFF;
	}
	&__socials{
		display: flex;
		justify-content: space-between;
		width: 260px;
	}
	&__social{
		width: 60px;
		height: 58px;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		img,svg{
			width: 80%;
			height: 80%;
			object-fit: contain;
		}
	}

	&__form{
		margin-top: 44px;
		width: 100%;
	}
	&__input{
		display: block;
		width: 100%;
		margin-bottom: 29px;
		input{
			width: 100%;
			font-family: $mainFont;
			font-style: normal;
			font-weight: 500;
			font-size: 18px;
			line-height: 130%;
			font-feature-settings: 'pnum' on, 'lnum' on;
			color: #FFFFFF;
			text-indent: 30px;
			border: 1px solid #FFFFFF;
			box-sizing: border-box;
			border-radius: 5px;
			height: 63px;
			background: transparent;
			&::placeholder{
				font-family: $mainFont;
				color: #FFFFFF;
			}
			&:focus{
				outline: none;
			}
		}
	}
	&__agree{
		&-checkbox{
			cursor: pointer;
			input{
				position: absolute;
				width: 0;
				height: 0;
				visibility: hidden;
				opacity: 0;
			}
			span{
				width: 20px;
				height: 20px;
				border: 1px solid #FFFFFF;
				box-sizing: border-box;
			}
		}
	}
}
